import { Injectable } from '@angular/core';
import {FormControl,FormGroup} from '@angular/forms';
import {Observable,Subject,throwError as ObservableThrowError} from 'rxjs'
import {HttpClient,HttpHeaders, HttpErrorResponse,HttpParams} from '@angular/common/http'

import 'rxjs/add/operator/map';
import {catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
  export class UserrightsService {

  private resturl='https://jsonplaceholder.typicode.com'
  private subject=new Subject<any>();
  //userdetaillist:any;
  constructor(private http:HttpClient) { }

getrestusers():Observable<any>
{
return this.http.get(`${this.resturl}/posts`).map((res:any)=>res);
}


  getuserdetails():Observable<any>
  {
    //const api='';
   return this.http.get('https://jsonplaceholder.typicode.com/users');
  }

  sendmessage(messages:string)
  {
this.subject.next({text:messages});
  }

  clearmessage()
  {
    this.subject.next();
  }

  getMessage(): Observable<any> {
    return this.subject.asObservable();
}

//new get

getcomments():Observable<any>{

  //https://jsonplaceholder.typicode.com/posts/1/comments
return this.http.get('https://jsonplaceholder.typicode.com/posts/1/comments').pipe(catchError(this.errorHandler))
}

errorHandler(error:HttpErrorResponse)
{
return ObservableThrowError(error.message||"server error")
}

getcommentsbyid():Observable<any>{

  let params1=new HttpParams().set('userid','1');
  return this.http.get('https://jsonplaceholder.typicode.com/posts/1/comments',{params:params1})
}


}
