import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CustomreportComponent } from './customreport/customreport.component';
import {FormsModule} from '@angular/forms';
import { CustomchildComponent } from './customchild/customchild.component';
import { CartComponent } from './cart/cart.component';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: 'Customreport', component: CustomreportComponent },
  { path: 'Customchild', component: CustomchildComponent },
  { path: 'Cart', component: CartComponent },
];

@NgModule({
  declarations: [CustomreportComponent, CustomchildComponent, CartComponent],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes)
  ],
  exports:[CustomreportComponent,CustomchildComponent]
})
export class ReportsModule { }
