import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserdetailsComponent } from './userrights/userdetails/userdetails.component';
import { UserComponent } from './userrights/user/user.component';
import {SidemenuComponent} from './userrights/sidemenu/sidemenu.component';
import { HerosComponent } from './heros/heros.component';
import { CustomreportComponent } from './reports/customreport/customreport.component';
import { CustomchildComponent } from './reports/customchild/customchild.component';
import { CartComponent } from './reports/cart/cart.component';



const routes: Routes = [  
  {path: '', redirectTo: '/SidemenuComponent', pathMatch: 'full' },
  { path: 'user',      component: UserComponent },
  { path: 'userdetails', component: UserdetailsComponent },
  { path: 'heros', component: HerosComponent },
  { path: 'ReportsModule', loadChildren: './reports/reports.module' }
  
  // {path: 'Customreport', component: CustomreportComponent },
  // {path: 'Customchild', component: CustomchildComponent },
  // {path:'Cart',component:CartComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
